:host {
  display: flex;
  flex-direction: column;
  height: 100%;
  min-height: 0;
}

.packing-mobile-panel {
  min-height: 0;
  overflow: hidden;
}

@media (max-width: 768px) {
  :host {
    width: 100%;
    max-width: 100%;
    overflow: hidden;
  }
  .packing-mobile-panel {
    width: 100%;
    max-width: 100%;
  }
  .packing-mobile-footer {
    padding-bottom: calc(1.5rem + env(safe-area-inset-bottom));
  }
}
